.header {
  z-index: 3;
  height: 5rem;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  font-weight: 700;
  left: 0;
  top: 0;
  position: fixed;
}
.header img {
  height: 5rem;
  max-height: 5rem;
}
.header .header__img {
  min-width: 4rem;
}
.header nav {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
.header .header__list {
  width: 90%;
  display: flex;
  justify-content: flex-end;
}
.header .header__list ul {
  display: flex;
  gap: 3rem;
}
.header .header__list ul li:last-of-type {
  margin-right: 3rem;
}
.header .header__list ul li a {
  font-size: 1.5rem;
}

.active-tab {
  color: #E08709;
}

b {
  color: white;
  font-size: 0rem;
}

.imagen-small {
  max-width: 15rem;
}

.nosotros {
  margin: 0 auto;
  width: 60%;
