}
.nosotros h1 {
  font-weight: 700;
  font-size: 5rem;
}
.nosotros p {
  color: #FFFFFF;
  font-size: 2.1rem;
}

.mt {
  margin-top: 10rem;
}

.fw {
  font-weight: 700;
}

.ta {
  text-align: center;
}

.table-container {
  margin-top: 10rem;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 10rem;
}

.table-productos {
  width: 100%;
  border-spacing: 0;
}
.table-productos thead {
  font-size: 2rem;
  font-weight: 700;
  background-color: #71B100;
  color: #FFFFFF;
}
.table-productos tbody {
  color: #FFFFFF;
}
.table-productos td {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  border-bottom: 0.5rem solid #333333;
}
.table-productos td:first-of-type {
  width: 5%;
}
.table-productos td:nth-child(2) {
  width: 30%;
}
.table-productos td:nth-child(3) {
  width: 25%;
}
.table-productos td:nth-child(3) img {
  max-width: 50%;
  margin: 0 auto;
}
.table-productos td:nth-child(4) {
  width: 10%;
}

.botones-div {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.table {
  width: 100rem;
}
.table .boton-verde {
  margin-bottom: 2rem;
}

.alerta {
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 2rem;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
}
.alerta.exito {
  background-color: #71B100;
}
.alerta.error {
  background-color: rgb(186, 35, 35);
}

@media (max-width: 1024px) {
  .table {
    width: 90%;
  }
  td {
    font-size: 1.5rem !important;
  }
}
.contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.formulario {
  width: 80rem;
  padding: 3rem;
  border-radius: 2rem;
  background-color: #333333;
  color: #FFFFFF;
}
.formulario input, .formulario textarea, .formulario select {
  border-radius: 0.5rem;
  border: none;
  padding: 1.4rem;
  width: 70%;
}
.formulario label {
  display: inline-block;
  width: 30%;
  font-size: 2rem;
}
.formulario .input {
  display: flex;
  margin-bottom: 3rem;
  width: 100%;
  margin-right: 0;
}

.input input[type=file] {
  width: 50%;
}

@media (max-width: 768px) {
  .input {
    display: flex;
    flex-direction: column;
  }
  .input input, .input textarea, .input select {
    width: 100% !important;
  }
  .input label {
    width: 100%;
  }
}
@media (max-width: 1024px) {
  .formulario {
    width: 90%;
  }
}
.boton-rojo-block {
  display: block;
  width: 8rem;
  background-color: rgb(186, 35, 35);
  color: #FFFFFF;
  border: none;
  border-radius: 3rem;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.3rem;
  font-weight: 700;
  cursor: pointer;
}

.boton-amarillo-block {
  display: block;
  width: 10rem;
  line-height: 5rem;
  background-color: #E08709;
  border-radius: 3rem;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.3rem;
  font-weight: 700;
}

.boton-azul {
  display: block;
  width: 10rem;
  background-color: rgb(65, 65, 233);
  color: #FFFFFF;
  border: none;
  border-radius: 3rem;
  padding: 1rem;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.3rem;
  font-weight: 700;
  cursor: pointer;
  margin-bottom: 2rem;
}

.boton-div {
  width: 80rem;
}

.boton-container {
  display: flex;
  width: 100%;
  justify-content: center;
  margin: 0;
  padding: 0;
}
.boton-container input[type=submit] {
  width: 100%;
}

.boton-enviar {
  display: block;
  width: 100%;
  background-color: rgb(65, 65, 233);
  padding: 0.7rem;
  text-transform: uppercase;
  cursor: pointer;
  color: #FFFFFF;
  font-weight: 700;
  border: none;
  border-radius: 1rem;
}

@media (max-width: 1024px) {
  .boton-div {
    width: 90%;
  }
}
.login {
  width: 60rem;
  margin: 0 auto;
}
.login h1 {
  margin-top: 10rem;
  font-weight: 700;
}

.login__formulario {
  background-color: #FFFFFF;
  width: 100%;
  padding: 5rem;
  border-radius: 2rem;
}
.login__formulario p {
  font-size: 1.5rem;
  text-align: center;
}
.login__formulario p a {
  color: rgb(65, 65, 233);
