}
.login__formulario .boton-container {
  margin-top: 2rem;
}
.login__formulario .login__campo {
  margin-bottom: 3rem;
}
.login__formulario .login__campo label {
  width: 25%;
  display: inline-block;
}
.login__formulario .login__campo input {
  width: 74%;
