}

@media (max-width: 1024px) {
  .login {
    width: 60%;
  }
}
@media (max-width: 768px) {
  .login {
    width: 90%;
  }
  .login__formulario {
    padding: 5rem 2rem;
  }
  .login__formulario input {
    width: 59%;
  }
  .login__formulario label {
    width: 40%;
  }
}
svg:hover {
  cursor: pointer;
}

.container2 {
  width: 100%;
  min-height: 20vh;
  overflow: hidden;
}

.slider {
  position: relative;
  width: 100%;
  margin-top: 5rem;
}

.slider__container {
  display: grid;
  align-items: center;
  gap: 1em;
}

.slider__body {
  grid-column: 1/2;
  grid-row: 1/2;
  display: grid;
  width: 100%;
}
.slider__body .slider__picture {
  opacity: 0;
  transition: opacity 3s;
}

.slider__body--show .slider__picture {
  opacity: 1;
  transition: opacity 3s;
}

.div-flecha {
  opacity: 1;
  position: absolute;
  display: flex;
  align-items: center;
  height: 100%;
  z-index: 2;
}
.div-flecha svg {
  background-color: #f2f2f2;
  border-radius: 3rem;
  width: 6rem;
  height: 6rem;
}
.div-flecha.left {
  left: 0;
}
.div-flecha.der {
  right: 0;
}

.slider__img {
  pointer-events: none;
  width: 100%;
}

figure {
  margin: 0;
}

.listado {
  width: 100%;
  margin-bottom: 3rem;
  margin-top: 2rem;
}

.search {
  display: flex;
  margin: 0 auto;
  width: 100rem;
  justify-content: flex-end;
}
.search label {
  margin-right: 1rem;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  align-items: center;
}
.search select {
  margin-right: 2rem;
  width: 10rem;
  border: none;
}
.search input {
  padding: 1rem;
  border: none;
  border-radius: 0.7rem;
  width: 20rem;
}
.search p#par {
  display: inline-block !important;
  width: 100% !important;
}

.listado__box {
  display: grid;
  margin: 0 auto;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
  color: black;
  width: 100rem;
}
.listado__box .listado__item {
  background-color: #f2f2f2;
  color: black;
  width: 100%;
}
.listado__box h1, .listado__box h2 {
  color: black;
}
.listado__box .precio span::before {
  content: "$";
}
.listado__box .precio {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #71B100;
  font-size: 2rem;
  font-weight: 700;
}
.listado__box span.titulo {
  display: block;
  width: 100%;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
}
.listado__box .listado__img img {
  max-width: 80%;
  margin: 0 auto;
}

@media (max-width: 1024px) {
  .listado__box {
    width: 90%;
  }
  span.titulo {
    font-size: 1.5rem !important;
  }
  .search {
    width: 90% !important;
  }
  .precio span {
    font-size: 1.5rem !important;
  }
  .paginas {
    width: 90% !important;
  }
}
@media (max-width: 768px) {
  .listado__box {
    grid-template-columns: 1fr 1fr;
    width: 90%;
  }
  .search {
    display: grid;
    grid-template-columns: 12.5% 32.5% 7.5% 37.5%;
    grid-template-rows: 0.5fr 0.8fr;
    column-gap: 3.5%;
    row-gap: 2rem;
  }
  .search label {
    display: flex;
    align-items: center;
    margin: 0;
  }
  .search select {
    margin-right: 1rem;
    width: 100%;
  }
  .search input {
    width: 100%;
  }
}
