  .header__list {
    display: none !important;
  }
  .header__list.header__list--show {
    display: flex !important;
    flex-direction: column;
  }
  .header2 {
    height: 50vh;
    display: flex;
    flex-direction: column;
  }
  .header2 .header__img {
    min-width: 100%;
  }
  .header2 nav {
    display: flex;
    flex-direction: column;
  }
  .header2 nav img {
    height: 7rem;
    max-height: 7rem;
  }
  .header2 .header__list ul {
    gap: 1rem;
    width: 100%;
